.footer{
    width: 100%;
    padding: 6rem 0;
}

.footer-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 40px;
}

.left{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}
.location{
    display: flex;
    margin-bottom: 0.8rem;

}
.location p{
    line-height: 30px;
}

.rigth{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}


.social{
    margin-top: 1rem;

}

h4{
    font-size: 1.2rem;
    padding-bottom: 0.7rem;
}

p{
    font-size: 1.2rem;
    
}
.btns{
    padding:12px 35px;
    font-size: 1rem;
    text-transform: uppercase;
    background-color: yellow;
    color: black;
    border: 1px solid white;
    font-weight:bolder;
    cursor: pointer;
    margin-left: 25rem;
}